<template>
  <v-container :fluid="true">
    <v-row>
      <v-col cols="12">
        <h1 class="font-weight-bold mb-3">Все пользователи</h1>
        <v-breadcrumbs class="px-0 py-0 mb-3" :items="items" divider="/">
          <template v-slot:item="{ item }">
            <v-breadcrumbs-item
              :disabled="item.disabled"
              :exact="true"
              :to="{ name: item.to.name }"
            >
              {{ item.text.toUpperCase() }}
            </v-breadcrumbs-item>
          </template>
        </v-breadcrumbs>
      </v-col>
    </v-row>
    <v-row>
      <v-col cols="12">
        <div class="mb-3">
          <UsersTable @changeFilter="change" :users="users" />
        </div>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import UsersTable from "../../components/Tables/UsersTable";

export default {
  name: "Users",
  metaInfo: {
    title: "Шестерёнка | Пользователи"
  },
  data() {
    return {
      items: [
        {
          text: "Панель",
          disabled: false,
          to: { name: "Orders" }
        },
        {
          text: "Все пользователи",
          disabled: true,
          to: { name: "Users" }
        }
      ]
    };
  },
  methods: {
    ...mapActions({
      loadUsers: "fetchUsers",
      loadUser: "fetchUser"
    }),
    change(filter) {
      console.log(filter);
    }
  },
  computed: {
    ...mapGetters({
      users: "getUsers",
      user: "getUser"
    })
  },
  components: {
    UsersTable
  },
  async beforeMount() {
    await this.loadUsers();
  }
};
</script>

<style scoped></style>
