<template>
  <v-data-table
    :headers="headers"
    :items="users"
    :page="filter.page"
    :options="{ page: 1, itemsPerPage: 10 }"
    :items-per-page="filter.itemsPerPage"
    :server-items-length="40"
    v-on:pagination="updatePaginate"
    class="elevation-4"
  >
    <template v-slot:top>
      <v-toolbar flat>
        <v-toolbar-title>Все пользователи</v-toolbar-title>
        <v-divider class="mx-4" inset vertical></v-divider>
        <v-spacer></v-spacer>
      </v-toolbar>
    </template>

    <template v-slot:item.id="{ item }">
      {{ item.id }}
    </template>

    <template v-slot:item.name="{ item }">
      {{ item.information.first_name }}
    </template>

    <template v-slot:item.surname="{ item }">
      {{ item.information.last_name }}
    </template>

    <template v-slot:item.actions="{ item }">
      <v-icon small class="mr-2" @click="editItem(item)">
        mdi-pencil
      </v-icon>
      <v-icon small @click="deleteItem(item)">
        mdi-delete
      </v-icon>
    </template>
    <template v-slot:no-data>
      <v-btn color="primary">
        Reset
      </v-btn>
    </template>
  </v-data-table>
</template>

<script>
export default {
  name: "UsersTable",
  data() {
    return {
      headers: [
        {
          text: "Идетификатор",
          align: "start",
          sortable: false,
          value: "id"
        },
        { text: "Имя", value: "name", sortable: false },
        { text: "Фамилия", value: "surname", sortable: false },
        { text: "Действия", value: "actions", sortable: false }
      ],
      filter: {
        page: 1,
        pageCount: 3,
        itemsPerPage: 20
      },
      options: {
        page: 1,
        itemsPerPage: 20
      }
    };
  },
  methods: {
    editItem() {},
    deleteItem() {},
    updatePaginate(paginate) {
      this.filter = { ...paginate };
    }
  },
  watch: {
    filter: {
      deep: true,
      handler() {
        this.$emit("changeFilter", this.filter);
      }
    }
  },
  props: {
    users: {
      type: Array,
      required: true
    }
  }
};
</script>
